import React, { createContext, useContext, useState, useEffect } from 'react'

// Cr√©ation du contexte pour la gestion du th√®me
const ThemeContext = createContext()

// Hook personnalis√© pour utiliser le contexte du th√®me
export const useTheme = () => {
  const context = useContext(ThemeContext)
  if (!context) {
    throw new Error('useTheme must be used within a ThemeProvider')
  }
  return context
}

// Provider du contexte de th√®me
export const ThemeProvider = ({ children }) => {
  const [theme, setTheme] = useState(() => {
    // R√©cup√©ration du th√®me depuis le localStorage ou d√©faut 'dark'
    return localStorage.getItem('portfolio-theme') || 'dark'
  })

  // Fonction pour basculer le th√®me
  const toggleTheme = () => {
    const newTheme = theme === 'dark' ? 'light' : 'dark'
    setTheme(newTheme)
    localStorage.setItem('portfolio-theme', newTheme)
  }

  // Application du th√®me au body
  useEffect(() => {
    document.body.setAttribute('data-theme', theme)
  }, [theme])

  const value = {
    theme,
    toggleTheme,
    isDark: theme === 'dark'
  }

  return (
    <ThemeContext.Provider value={value}>
      {children}
    </ThemeContext.Provider>
  )
}
